@import url(https://fonts.googleapis.com/css?family=Montserrat:400,800);
.trigger {
  padding: 0 24px;
  font-size: 18px;
  line-height: 64px;
  cursor: pointer;
  transition: color 0.3s;
}

.trigger:hover {
  color: #1890ff;
}

.logo {
  height: 32px;
  margin: 16px;
  background: rgba(255, 255, 255, 0.3);
}

.site-layout .site-layout-background {
  background: #fff;
}

.logout-button {
  margin-right: 20px;
}

.dashboard-content {
  margin: 24px 16px;
  padding: 24px;
  min-height: 280px;
}

.loading-content {
  display: flex;
  justify-content: center;
  align-items: center;
}

.auth-container {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
  position: relative;
  overflow: hidden;
  width: 768px;
  max-width: 100%;
  min-height: 480px;
}

.auth-container-box {
  position: absolute;
  top: 0;
  height: 100%;
}

.auth-form-container {
  background-color: #ffffff;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 50px;
  height: 100%;
  text-align: center;
}

.auth-input {
  background-color: #eee !important;
  border: none;
  padding: 12px 15px;
  width: 100%;
}

.auth-input input {
  background-color: #eee !important;
}

.auth-title {
  font-weight: bold;
  margin: 5;
  font-size: 3em;
  padding: 5px;
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
}

.overlay {
  background: #0575e6; /* fallback for old browsers */ /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(
    to bottom,
    #559bd4,
    #1890ff
  ); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  position: relative;
  left: -100%;
  height: 100%;
  width: 200%;
}

.overlay-panel {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 0 40px;
  text-align: center;
  top: 0;
  height: 100%;
  width: 50%;
}

.overlay-right {
  right: 0;
}

.text-header {
  color: white;
  font-size: 2em;
}

body {
    background: #f6f5f7;
    font-family: 'Montserrat', sans-serif;
}

.auth-body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.printer-body {
    display: flex;
    justify-content: center;
    align-items: center;
}

.spinner-body {
    width: 100%;
    min-height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.public-screen-body {
    overflow-y: hidden;
    overflow-x: hidden; 
}

.title {
    font-size: 2em;
}

.show {
    display: inherit;
}

.hidde {
    display: none;
}
.queue-card-container {
  width: 350px;
  margin: 5px;
}

.queue-card-body-actual-number-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.queue-card-body-actual-number-text {
  font-size: 2.3em;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 2px solid rgb(187, 186, 186);
  border-radius: 10px;
  padding: 30px;
}

.queue-card-body-info {
  display: flex;
  justify-content: space-between;
}

.queue-card-body-title-text {
    font-size: 1.3em;
    font-weight: 200;
}

.queue-card-body-info-text {
    color: #559bd4;
    font-weight: bold;
}
.printer-option-button {
  font-size: 3.75vw;
  font-weight: 600;
  margin: 0.3em;
  min-width: 120%;
  height: 100%;
  border-radius: 50px;
  box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.printer-message-container {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 50px;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
}

.printer-message-number-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.printer-message-default {
  font-size: 5em;
  width: 100%;
}

.printer-message-number {
  font-size: 6em;
  font-weight: bold;
  border: 2px solid rgb(187, 186, 186);
  border-radius: 30px;
  padding: 20px;
}

.ps-header-container {
    height: 10vh;
    width: 100vw;
    padding: 10px;
    background: #95B8D1;
    font-size:calc(100% + 1.3vw);
    font-weight: 500;
    color:black;
}

.ps-content-container {
    height: 90vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.ps-img-container{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: cover;
}

.ps-img {
    width: 100%;
  height: 100%;
  object-fit: cover;
  border: 1px solid black;
}

.ps-content-list-numbers {
    background: #95B8D1; 
    background: linear-gradient(
      to bottom,
      #95B8D1,
      #1890ff
    ); 
    height: 100%;
    width: 100%;
    font-size: 5vw;
    border-top: 1px solid black;
}

.ps-number-call {
    display: flex;
    justify-content: space-around;
    box-shadow: 0 14px 28px rgba(0, 0, 0, 0.25), 0 10px 10px rgba(0, 0, 0, 0.22);
    margin: 12px;
    background-color: #D6EADF;
    border-radius: 25px;

}

.ps-number-divider {
    border-right: 1px solid black;
}

.zoom-effect {
    -webkit-animation-name: zoom;
            animation-name: zoom; 
    -webkit-animation-duration: 1.7s; 
            animation-duration: 1.7s; 
    -webkit-animation-iteration-count: infinite; 
            animation-iteration-count: infinite;
    -webkit-animation-timing-function: linear;
            animation-timing-function: linear;
}

@-webkit-keyframes zoom {
    0% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    50% {
        -webkit-transform: scale(0.97,0.97);
                transform: scale(0.97,0.97);
    }
    100% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
}

@keyframes zoom {
    0% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
    50% {
        -webkit-transform: scale(0.97,0.97);
                transform: scale(0.97,0.97);
    }
    100% {
        -webkit-transform: scale(1,1);
                transform: scale(1,1);
    }
}

